import React, { Component } from 'react';
import logo from './JUMP_Bleed.svg';
import './App.css';


const G_FORMACTION = "https://script.google.com/macros/s/AKfycbzlydGL03jh3jxkNgDuMoDo7hG5c6123TnLxnIJjXuUD4vnL6k/exec"

class App extends Component {
  constructor(props) {
      super(props);
      
      this.state = {
        name: null,
        van: null,
        scooter_qr: null,
        reason: null,
        icon: null,
        notes: "",
        lat: null,
        lon: null,
        scooter_list:[],
        alert_banner:[],
        form_list:[]
      }

      this.flags = {
        name: null,
        van: null,
        scooter_qr: null,
        reason: null,
        icon: null,
        notes: null,
        lat: null,
        lon: null,
        scooter_list: null,
        alert_banner: null,
        form_list: null
      }



      this.addButton = this.addButton.bind(this);
      this.setVan = this.setVan.bind(this);
      this.setName = this.setName.bind(this);
      this.setName = this.setName.bind(this);
      this.setReason = this.setReason.bind(this);
      this.setLatLon = this.setLatLon.bind(this);
      this.setScooter = this.setScooter.bind(this);
      this.setNotes = this.setNotes.bind(this);
      this.getGPS = this.getGPS.bind(this);
      this.validate = this.validate.bind(this);
      this.addNewScooter = this.addNewScooter.bind(this);
      this.removeAllScooters = this.removeAllScooters.bind(this);
      this.submitForms = this.submitForms.bind(this);
      this.formSuccess = this.formSuccess.bind(this);
      this.formFail = this.formFail.bind(this);



    }

  componentDidMount(e) {

    if (window.localStorage.getItem("name")){
      this.setState({name : window.localStorage.getItem("name")})
      
      console.log(window.localStorage.getItem("name"))
    } else {
      console.log("no name")
    }

    if (window.localStorage.getItem("van")) {
        this.setState({van : window.localStorage.getItem("van")})
      } else {
        console.log("no van")
    }
  }
  
  componentWillUpdate() {
    console.log("FLAGS:")
    for (var f in this.flags) {
      if(this.flags[f]){
        console.log(f + "->" + this.flags[f])
      }
    }
  
  }
  componentDidUpdate(e) {
    console.log("updated", this.state)
    //window.localStorage.clear();
    if (this.state.name){
      window.localStorage.setItem("name", this.state.name);
    }
    if (this.state.van){
      window.localStorage.setItem("van", this.state.van);
    }
    
    
  
    console.log("state saved", window.localStorage.getItem('name'));
  }


  setFlag(flagArray){

  }

  addButton(label, icon, callback){
    const buttonElem = (<button type="button" name={label} className="btn btn-select btn-lg col-6 col-md-2" onClick={callback}><i className={ icon }></i> <br/> { label } </button>);
    return buttonElem;
  }

  setVan(e){
    var van_number = parseInt(e.target.value);
    if (!(Number.isInteger(van_number))) {
      van_number=null;
    } 

    this.setState({van:van_number});

  }

  setName(e){
    const ft_name = e.target.value;
    this.setState({name:ft_name});

  }

  setReason(e){
    this.getGPS();
    const pickup_reason = e.currentTarget.name;
    console.log(e.currentTarget.name)
    this.setState({reason:pickup_reason, icon:e.currentTarget.children[0].className});
  }

  setScooter(e){
   const scooter = e.target.value;
   this.setState({scooter_qr:scooter});
  }

  setNotes(e){
    const new_note = e.target.value;
    this.setState({notes:new_note});
  }

  setLatLon(gps) {
    const curr_lat = gps.coords.latitude;
    const curr_lon = gps.coords.longitude;
    console.log(curr_lon.toPrecision(5));
    this.setState({lat:curr_lat, lon:curr_lon});
    
  }

  getGPS() {
    var gps_string="GPS is not available";
    if (navigator.geolocation) {
      gps_string="Loading GPS...";
      navigator.geolocation.getCurrentPosition(this.setLatLon);
    } 

    if ((this.state.lat == null)||(this.state.lon == null)) {
      this.setState({lat:gps_string})
    }

    console.log(gps_string);
  }

  validate(){
    var isValid = false;

    if (((this.state.name == null)||(this.state.name === ""))||(this.state.van == null)){
      const alert = <div className="alert alert-warning" role="alert">
                      Check Name / Van #
                      </div>
      console.log("no name or van")
      this.setState({ alert_banner: [...this.state.alert_banner, alert] });
    } else {


    if (this.state.reason){
      if ((this.state.lat == null) || (this.state.lon == null)){
        const alert = <div className="alert alert-warning" role="alert">
                      GPS error
                      </div>
        this.setState({ alert_banner: [...this.state.alert_banner, alert] });

      } else {
        if (!(Number.isInteger(parseInt(this.state.scooter_qr, 10)))) {
          this.setState({scooter_qr:"0000"})
        }
        console.log("valid")
        isValid = true;
        this.setState({alert_banner: null})

      }
    } else {
      const alert = <div className="alert alert-danger" role="alert">
                      Select a Pick Up Reason
                    </div>
      this.setState({ alert_banner: [...this.state.alert_banner, alert] });
    }
  }

    console.log(isValid)
    return isValid;
  }

  validateGPS() {
    var gpsReady = false

    return gpsReady;
  }

  addNewScooter() {
    var valid = this.validate()
    if (valid) {
    var formData = new FormData();
    console.log("appending to form")
    formData.append("name", this.state.name);
    formData.append("van", this.state.van);
    formData.append("number", this.state.scooter_qr);
    formData.append("reason", this.state.reason);
    formData.append("notes", this.state.notes);
    formData.append("lat", this.state.lat);
    formData.append("lon", this.state.lon);
    
    this.setState({ form_list: [...this.state.form_list, formData] });

    console.log(formData.values());
    const jsx = <li key={this.state.scooter_list.length} className="list-group-item bg-dark my-3 ">
                  <p className="text-muted text-tiny mt-1"> Scooter: {this.state.scooter_qr} | {this.state.reason} <i className={this.state.icon}></i> | {this.state.lat.toPrecision(6)}, {this.state.lon.toPrecision(7)}</p>
                  <p className={(this.state.notes != "") ? "d-block text-muted text-tiny mb-1" : "d-none"}> Notes: {this.state.notes} </p>
                </li>
    this.setState({ scooter_list: [...this.state.scooter_list, jsx], scooter_qr: null, notes: null })
  }

  }

  removeAllScooters(e) {
    this.setState({scooter_list:[]})
  }

  submitForms(){
    
    
    for (var i = this.state.form_list.length - 1; i >= 0; i--) {
      console.log(this.state.form_list[i].get("name"));
      fetch(G_FORMACTION, { method: 'POST', mode: "no-cors", body: this.state.form_list[i]})
      .then(() => this.formSuccess())
      //.then(response => console.log('Success!', response))
      .catch(error => this.formFail(error))
      //.catch(error => console.error('Error!', error.message))
      console.log(this.state.form_list[i])
    }
    
    

  }

  formSuccess(){
    const success = <div className="alert alert-success" role="alert">
                      Success!
                      </div>

    this.setState({
        scooter_qr: null,
        reason: null,
        icon: null,
        notes: "",
        lat: null,
        lon: null,
        scooter_list:[],
        alert_banner: [success],
        form_list:[]
      })

  }

  formFail(error){
    console.error('Error!', error.message)
    const fail = <div className="alert alert-success" role="alert">
                      {error.message}
                      </div>

    this.setState({ alert_banner: [...this.state.alert_banner, fail] });
  }



  render() {
    return (
      <div className="App">
        <img className="App-logo" alt="JUMP" src={logo} />
        
        <header className="App-header">

          <h1 className="display-4 text-center">Scooter Recovery</h1>
          <div className="d-flex justify-content-end bd-highlight flex-row mt-0">
              <input type="text" placeholder={this.state.name ? this.state.name : "Name"} className="form-control col m-2 flex-fill" onChange={(e) => this.setName(e)} ></input>
              <input type="number" placeholder={this.state.van ? this.state.van : "Van #"} className="form-control col m-2 flex-fill" onChange={(e) => this.setVan(e)} ></input>
          </div>
          
        </header>
        <div className="App-body">
          

          <div className="row container-fluid m-2">
            
            {this.addButton("Broken","fas fa-tools", (e) => this.setReason(e))}
            {this.addButton("Vandalized","fas fa-exclamation-triangle", (e) => this.setReason(e))}
            {this.addButton("Soft Broken","fas fa-cogs", (e) => this.setReason(e))}
            {this.addButton("Stolen/Lost","fas fa-eye-slash", (e) => this.setReason(e))}
            {this.addButton("Recovery","fas fa-plus", (e) => this.setReason(e))}
            {this.addButton("Rebalance","fas fa-sign-out-alt", (e) => this.setReason(e))}
          </div>
          <div className="card text-white bg-dark text-center m-2 mb-5">
            <div className="card-header">
            <p className="text-muted lead m-0">{this.state.reason ? this.state.reason : "Select Pick Up Reason"} <i className={this.state.icon}></i></p>  

            </div>
            <div className="card-body">
              <div className="input-group">
                <input type="number" placeholder="Scooter #" className="form-control" onFocus={e => (e.target.value="")} onChange={(e) => this.setScooter(e)}></input>


                <input type="text" placeholder="Notes" className="form-control" onChange={(e) => this.setNotes(e)}></input>
                <div className="input-group-append">
                  <button className="btn btn-outline-secondary" onClick={(e) => this.addNewScooter(e)} type="button"><i className="fas fa-plus-circle"></i> Add</button>
                </div>
              </div>
              <div className="text-tiny my-2">{this.state.alert_banner}</div>
              <ul className="list-group bg-dark text-center mx-auto mt-3">
                {this.state.scooter_list}
              </ul>
              <div className= {this.state.scooter_list.length ? "d-flex justify-content-end bd-highlight flex-row" : "d-none"}>
                <button className="btn btn-danger p-2 flex-fill" onClick={() => this.removeAllScooters()}><i className="fas fa-undo"></i> Reset</button>
                <p className="p-2 flex-fill" > </p>
                <button className="btn btn-success p-2 flex-fill" onClick={() => this.submitForms()}><i className="fas fa-success"></i> Submit</button>
              </div>

              
            </div>
            <div className="card-footer text-muted text-tiny">
              <div className="d-flex justify-content-center flex-row">
                <div className={this.state.name ? "d-block p-2" : "d-none"}>Field Tech: {this.state.name}</div>
                <div className={this.state.van ? "d-block p-2" : "d-none"}>Van: {this.state.van}</div>
                <div className={this.state.lat ? "d-block p-2" : "d-none"}>{this.state.lat}, {this.state.lon}</div>
              </div>
              
            </div>
          </div>
        </div>
          
         
          
          
      </div>
    );
  }
}

export default App;
